import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Rating } from '@mui/material'
import { useParams } from 'react-router-dom'
import './Details.css'
function DetailPage() {
  const { id, media_type } = useParams()


  const [Movie, setMovie] = useState([]);



  useEffect(() => {
    const fetchData = async () => {
      if (media_type === 'tv') {
        const data = await axios.get(`https://api.themoviedb.org/3/tv/${id}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`)
        setMovie(data.data);
        console.log(data.data);

      }
      else {
        const { data } = await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`)
        setMovie(data);
        console.log(data);
      }
    }

    fetchData();

  }, [id, media_type]);


  return (<div className="moviedetail">
    {
      Movie ? <div>
        <img className='backdropDetail' src={`https://image.tmdb.org/t/p/w500/${Movie.backdrop_path}`} alt="" />
        <div className='MovieDetailsContainer' >
          <img className='thumb' src={`https://image.tmdb.org/t/p/w500/${Movie.poster_path}`} alt="" />

          <div className='MovieDetail'>
            <h1>{Movie.name || Movie.original_title}</h1>
            <p>{Movie.overview}</p>
            {Movie.genres && Movie.genres.map(item => <span className='Genres' key={item.id} >{item.name}</span>)}
            <p><Rating precision={0.5} value={Movie.vote_average / 2} /></p>
            <p><span>Runtime : </span>  {Movie.episode_run_time} {Movie.runtime} mins</p>
          </div>

        </div>
      </div> : "No prop"
    }
  </div>
  )
}

export default DetailPage