import React from 'react'
import { Badge } from '@mui/material'
import "./MovieCard.css"

function MovieCard(props) {
    const { id, name, backdrop_path, original_title, title, release_date, voter_avg, media_type, overview, poster_path, first_air_date } = props
    return (
        <div className='movieCard'>
            <Badge className='badge' color={voter_avg>5 ? 'primary' : 'secondary'} badgeContent={ voter_avg}/>
            <img  className='CardImg' src={`https://image.tmdb.org/t/p/w500/${poster_path}`} alt="" />
            <div className='CardContent'>
                <div className='cardInfo '><h4> {title || name  }</h4> <span >{first_air_date|| release_date }</span></div>
   
            </div>
        </div>
    )
}

export default MovieCard