import axios from 'axios';
import React, { useState, useEffect } from 'react'
import MovieCard from '../MovieCard';
import { Pagination } from '@mui/material';
import { Link } from 'react-router-dom';



function Movies() {
  const [PageVal, setPageVal] = useState(2);
  const [Movies, setMovies] = useState([]);

  const handlePagination = (e) => {
    setPageVal(e.target.textContent);
    window.scroll(0,0);
  }
  useEffect(() => {
    async function fetchTrending() {
      const { data } = await axios.get(`https://api.themoviedb.org/3/trending/movies/day?api_key=${process.env.REACT_APP_API_KEY}&page=${PageVal}`);

     
      setMovies(data.results);

    }

    fetchTrending()


  }, [PageVal]);

  return (
    <div className='page'>
      <h1 className='PageHeading'>Movies</h1>
      <div className="PageMovies">
        {
          Movies.length > 0 && Movies.map(movie => {
            return <Link to={`/details/${movie.id}/${movie.media_type}`} key={movie.id} ><
              MovieCard 
              id={movie.id}
              name={movie.name}
              original_title={movie.original_title}
              title={movie.title}
              release_date={movie.release_date}
              voter_avg={movie.vote_average}
              media_type={movie.media_type}
              overview={movie.overview}
              poster_path={movie.poster_path}
              first_air_date={movie.first_air_date}
              backdrop_path={movie.backdrop_path}


            /> </Link>
          })
        }
      </div>
      <div className='pagination'>
        <Pagination  hideNextButton hidePrevButton onChange={handlePagination} count={200} />
      </div>

    </div>
  )
}

export default Movies