import React from 'react'
import { Link } from 'react-router-dom'
import WhatshotIcon from '@mui/icons-material/Whatshot';
import MovieCreationIcon from '@mui/icons-material/MovieCreation';
import LiveTvIcon from '@mui/icons-material/LiveTv';
import './Header.css'
function Header() {
  return (
    <div className='Header'>
      <Link className='linkItem' to={'/'}><img className='logoImage' src="/images/logo.gif " alt='' /> <h2 className='LogoText'>Pixel Movies</h2> </Link>
      <div className="links">
        <Link className='linkItem '  to={'/'}>
          <WhatshotIcon className='fire' />
          <span>Tranding</span>
        </Link>
        <Link  className='linkItem' to={'/movies'}>
          <MovieCreationIcon  />
          <span>Movies</span>
        </Link>
        <Link className='linkItem' to={'/tvsiries'}>
          <LiveTvIcon />
          <span>Series</span>
        </Link>
      </div>
    </div>
  )
}

export default Header